<template>
    <v-container style="text-align: center;">
        <v-avatar
                v-if="size == 1"
                class="thumbnail"
                style="width: 95%; padding-top: 95%; vertical-align: top; position: relative; margin-top: 5%; overflow: visible">
            <img
                    v-if="showImg"
                    style="width: 100%; position: absolute; top: 0; height: 100%; object-position: center; object-fit: cover;"
                    class="white--text align-end"
                    :aspect-ratio="1"
                    :src="imagePreview"
            >
            <v-btn
                    absolute
                    dark
                    fab
                    bottom
                    right
                    width="25%"
                    height="25%"
                    color="primary"
                    style="margin-bottom: 11%;"
                    @click="$refs.myFiles.click()"
                    class="button-open-camera"
            >
                <v-icon>mdi-camera</v-icon>
            </v-btn>
        </v-avatar>

        <v-avatar
                v-if="size == 0.5"
                class="thumbnail"
                style="
                position: relative;
                height: 20vh !important;
                width: 20vh !important;
                min-width: 120px;
                min-height: 120px;
                border: 6px solid #00abba;
                max-height: 180px;
                max-width: 180px;
                background: #00abba;
                overflow: visible">
            <img
                    v-if="showImg"
                    style="width: 100.5%; height: 100.5%; position: absolute; top: 0; object-position: center; object-fit: cover;"
                    class="white--text align-end"
                    :aspect-ratio="1"
                    :src="imagePreview"
            >
            <v-btn
                    absolute
                    dark
                    fab
                    width="28%"
                    height="28%"
                    color="primary"
                    style="min-width: 50px; min-height: 50px; bottom: -10px; right: 0px;"
                    @click="$refs.myFiles.click()"
            >
                <v-icon>mdi-camera</v-icon>
            </v-btn>
        </v-avatar>


        <input hidden ref="myFiles" @change="previewFiles" type="file" accept="image/*">

        <!--SnackBar-->
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
        <!--End SnackBar-->
    </v-container>

</template>

<script>
    import imageCompression from "browser-image-compression";
    import Vue from "vue";

    export default {
        name: "ProfileImageSelector",
        props: {
            size: String,
            imagePreviewInitialValue: String
        },
        data() {
            return {
                showImg: true,
                imagePreview: this.$store.getters['user/getGenderDefaultProfileImgByUserGender'],
                imageUpload: null,
                chooseYourProfileImageText: null,
                closeText: null,
                continueText: null,
                snackbar: false,
                snackbarText: ''
}
        },
        methods: {
            previewFiles() {
                if (this.$refs.myFiles.files.length > 0) {
                    if (!this.$refs.myFiles.files[0].type.includes('image/')) {
                        this.snackbarText = this.$i18n.t('messages.ASK_FOR_IMAGE_SELECTION');
                        this.snackbar = true;
                        return;
                    }

                    let imageFile = this.$refs.myFiles.files[0];

                    let options = {
                        'maxSizeMB': 0.5,
                        'maxWidthOrHeight': 500,
                        'useWebWorker': true
                    };

                    imageCompression(imageFile, options)
                        .then((compressedFile) => {
                            if (typeof FileReader === 'function') {
                                const reader = new FileReader();
                                reader.onload = (event) => {
                                    this.imgSrc = event.target.result;
                                    this.imagePreview = this.imgSrc;
                                    this.imageUpload = this.imagePreview.replace("data:image/jpeg;base64,", "").replace("data:image/png;base64,", "");
                                    this.showImg = false;
                                    Vue.nextTick(() => {
                                        this.showImg = true;
                                    })
                                };
                                reader.readAsDataURL(compressedFile)
                            } else {
                                this.snackbarText = this.$i18n.t('messages.ERROR_API_FILEREADER_NOT_SUPPORTED');
                                this.snackbar = true;
                            }
                        })
                        .catch(function (error) {
                            console.log(error.message);
                        });
                } else {
                    console.warn("Img error")
                }
            }
},
        watch: {
            imageUpload: function (image) {
                console.log(this.imagePreviewInitialValue)
                this.$emit('newImageSelected', image);
            }
        },
        created() {
            this.chooseYourProfileImageText = this.$i18n.t('messages.TEXT_CHOOSE_YOUR_PROFILE_IMAGE');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.continueText = this.$i18n.t('messages.TEXT_TO_CONTINUE');
        },
        mounted() {
            if (this.imagePreviewInitialValue) {
                this.imagePreview = this.imagePreviewInitialValue;
            }
        }
    }
</script>

<style scoped>

</style>