<template>
    <div class="img-back" style="position: relative; height: 100%!important;">
        <v-container class="login" style="position: relative; height: 100%; max-width: 400px">
            <v-row class="justify-center text-center">
                <v-card-title class="text-center justify-center pa-3 white--text">
                    {{chooseYourProfileImageText}}
                    &nbsp;<img style="width: 44px" src="/LogoWeBabble.png"/>
                </v-card-title>

                <v-col cols="12">
                    <v-card
                            align="center"
                    >
                        <!--<v-progress-linear
                            value="100"
                            height="10"
                            color="primary"
                            reactive
                            dark
                    ></v-progress-linear>-->
                        <v-container style="width: 80%">
                            <profile-image-selector
                                    size="1"
                                    @newImageSelected="newImageSelected($event)">
                            </profile-image-selector>
                        </v-container>
                        <v-container class="pa-3" style="padding-top: 20px!important;">
                            <v-btn block color="primary" @click="uploadUserProfileImage"
                                   :disabled="buttonIsDisabled">
                                {{continueText}}
                            </v-btn>
                        </v-container>
                    </v-card>
                </v-col>
            </v-row>

            <!--SnackBar-->
            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="primary"
                        text
                        @click="snackbar = false"
                >
                    {{closeText}}
                </v-btn>
            </v-snackbar>
            <!--End SnackBar-->
        </v-container>
    </div>
</template>

<script>
    import {mapActions} from 'vuex';
    import ProfileImageSelector from "../AuxInputComponents/ProfileImageSelector";

    export default {
        name: "SetYourProfileImage",
        components: {ProfileImageSelector},
        data() {
            return {
                showImg: true,
                imagePreview: this.$store.getters['user/getGenderDefaultProfileImgByUserGender'],
                imageUpload: null,
                snackbar: false,
                snackbarText: '',
                chooseYourProfileImageText: null,
                closeText: null,
                continueText: null
}
        },
        methods: {
            ...mapActions('user', ['setProfileImg', 'setRegistrationCompleted']),
            newImageSelected: function (image) {
                this.imageUpload = image;
            },

            uploadUserProfileImage() {
                let self = this;

                if (self.imageUpload) {
                    let resultSetProfileImg = this.setProfileImg(self.imageUpload);

                    resultSetProfileImg.then(() => {
                        let resultSetRegistrationCompleted = this.setRegistrationCompleted(self.imageUpload);

                        resultSetRegistrationCompleted.then(() => {
                            this.$router.replace('dashboard');
                        })
                            .catch(function (error) {
                                self.snackbarText = error;
                                self.snackbar = true;
                                return
                            });
                    })
                        .catch(function (error) {
                            self.snackbarText = error;
                            self.snackbar = true;
                            return
                        });
                } else {
                    this.$router.replace('dashboard');
                }
            }
},
        created() {
            this.chooseYourProfileImageText = this.$i18n.t('messages.TEXT_CHOOSE_YOUR_PROFILE_IMAGE');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.continueText = this.$i18n.t('messages.TEXT_TO_CONTINUE');
        },
        computed: {
            buttonIsDisabled: function () {
                return this.imageUpload == null;
            }
        }
    }
</script>

